footer {
  background-color: var(--vy-dk-cyan);
}

.footer {
  padding-top: 8.625rem;
  padding-bottom: 2.9375rem;
  position: relative;
}
@media (min-width: 40em) {
  .footer {
    padding-top: 11.875rem;
  }
}
.footer-build {
  position: absolute;
  bottom: 100%;
  left: 50%;
  width: 90%;
  max-width: 50rem;
  transform: translate(-50%, 50%);
  border-radius: 0.9375rem;
  box-shadow: 0 0.0625rem 0.3125rem #2e2e2e;
  padding: 2.875rem 0;
  background-color: var(--white);
  text-align: center;
}
.footer-build p {
  margin-bottom: 1.5625rem;
  font-family: var(--font-heading);
  font-weight: 600;
}
@media (min-width: 40em) {
  .footer-build p {
    margin-bottom: 2.4375rem;
    font-size: 2rem;
  }
}
.footer-build a {
  display: inline-block;
  border-radius: 6.25rem;
  padding: 0.6875rem 3.1875rem;
  color: var(--white);
  background-color: var(--pink);
  font-weight: 700;
  font-size: 0.625rem;
  transition: opacity 300ms ease-in;
}
@media (min-width: 40em) {
  .footer-build a {
    font-size: 1rem;
    padding: 1.8125rem 7.5rem;
  }
}
.footer-build a:hover {
  opacity: 0.7;
}
.footer-logo {
  display: inline-block;
  margin-bottom: 1.75rem;
}
.footer-logo img {
  width: 10.5rem;
  height: 1.75rem;
}
@media (min-width: 71.875em) {
  .footer-logo img {
    width: 15.3125rem;
    height: 2.5rem;
  }
}
.footer-copyright {
  color: var(--white);
  font-size: 0.8125rem;
  text-align: center;
}
@media (min-width: 71.875em) {
  .footer-copyright {
    font-size: 0.9375rem;
    text-align: right;
  }
}

.info {
  display: grid;
  gap: 3.4375rem;
  color: var(--white);
  font-size: 1rem;
}
@media (min-width: 71.875em) {
  .info {
    grid-template-columns: 1fr 1fr 0.5fr;
  }
}
.info__contact {
  display: flex;
  flex-direction: column;
  gap: 1.75rem;
  font-size: 1.25rem;
}
.info__contact div {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1.5625rem;
}
.info__contact div a {
  transition: color 200ms ease-in;
}
.info__contact div a:hover {
  color: var(--pink);
}
.info__contact--loc {
  color: var(--white);
  align-self: flex-start;
}
.info__contact--loc ~ span {
  color: var(--white);
  align-self: flex-start;
}
.info__contact--num {
  color: var(--white);
}
.info__contact--email {
  color: var(--white);
}
.info__links {
  display: grid;
  gap: 1.75rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 1.125rem;
}
.info__links a {
  color: var(--white);
  transition: text-decoration 300ms ease-in;
}
.info__links a:hover {
  text-decoration: underline;
}
@media (min-width: 71.875em) {
  .info__links {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: max-content;
    gap: 1.5rem;
  }
  .info__links--us {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .info__links--we {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .info__links--faq {
    grid-column: 1/2;
    grid-row: 3/4;
  }
  .info__links--career {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .info__links--blog {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .info__links--contact {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}
.info__socmed {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
  margin-bottom: 1.5625rem;
}
@media (min-width: 71.875em) {
  .info__socmed {
    justify-content: flex-start;
  }
}
.info__socmed a {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.0625rem solid var(--white);
  border-radius: 50%;
  width: 2.0625rem;
  height: 2.0625rem;
  color: var(--white);
  font-size: 1.125rem;
  transition: all 200ms ease-in;
}
.info__socmed a:hover {
  border-color: var(--pink);
  color: var(--pink);
}